import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import { TabBar } from 'antd-mobile';

class Pic extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'blueTab',
    };
  }
  render() {
    const { pathname } = this.props.location;
    return (
      <div style={ {
        position: 'fixed',
        width: '100%',
        bottom: 0
      }}>
        <TabBar
          unselectedTintColor="#949494"
          tintColor="red"
          barTintColor="white"
        >
          <TabBar.Item
            title="店铺"
            key="dianpu"
            icon={
              <i className='iconfont icon-dianpu'></i>                           
            }               
          ></TabBar.Item>
          <TabBar.Item
            icon={
              <i className='iconfont icon-kefu'></i>                           
            }  
            title="客服"
            key="kefu"    
          ></TabBar.Item>
          <TabBar.Item
            icon={
              <i className='iconfont icon-favourited'></i>                           
            }  
            title="收藏"
            key="favourited"
          ></TabBar.Item>
          <TabBar.Item
            icon={
              <p 
                style={{
                  width:'100px',
                  height:'30px',
                  backgroundColor:'rgb(255,171,1)',
                  borderTopLeftRadius:'30px',
                  borderBottomLeftRadius:'30px',
                  color:'white',
                  lineHeight:'30px'
                }}>加入购物车</p>                   
            }  
           
          ></TabBar.Item>
          <TabBar.Item
            icon={
              <p
                style={{
                  width:'100px',
                  height:'30px',
                  backgroundColor:'rgb(255,108,4)',
                  marginRight:'20px',
                  borderTopRightRadius:'30px',
                  borderBottomRightRadius:'30px',
                  color:'white',
                  lineHeight:'30px'
                }}
              >立即购买</p>                         
            }   
          ></TabBar.Item>  
        </TabBar>  
      </div>
    );
  }   
}
export default withRouter(Pic)