import React from 'react';
import {BrowserRouter as Router,Redirect,Route,Switch} from 'react-router-dom';
import {Button,TabBar} from 'antd-mobile';
import Home from './taobao/Home';
import Cart from './taobao/Cart';
import Pages from './taobao/Pages';
import Things from './taobao/Things'
import Dingdan from './taobao/Dingdan'
import My from './taobao/My'
import Search  from './taobao/Search'

const App=()=>{
	return <Router>
		<Switch>
			<Route exact path='/home' component={Home} />
			<Route path='/cart' component={Cart} />
			<Route path='/pages' component={Pages} />
			<Route path='/things' component={Things} />
			<Route path='/dingdan' component={Dingdan} />
			<Route path='/my' component={My} />
			<Route path='/search' component={Search} />
			<Redirect to='/home' />
		</Switch>
	</Router>
}

export default App;